.container {
    flex: 0 0 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
p{
    margin: 5px;
}
.box{

    display:flex;
    flex-direction: column;
    width:50%;
    justify-content:space-between;
    background-color: blueviolet;
    border-radius: 15px;
    margin: 5px;
    padding: 0 15px;
    margin-left: auto;
}
.date{
    align-self: flex-end;
    font-size: .8em;
}
textarea{
    height: 5em;
    margin: 0;
}
.input{
    font-size: 18px ;
    /* height: 5em; */
    margin: 0;
    padding: 10px;
    width:50%;
    margin:0 auto;
    border: 2px solid black;
    border-radius:15px
}
.input:focus{
    outline: none;
}
.button{
    margin: 0 auto;
    width: 50%;

}
.holder{
    flex: 0 0 50%;
    overflow-y: scroll;
    border: 3px solid red;
    border-radius: 5px;
}